.portfolio-work-main-container-button{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 7.5vh;
    gap: 3vw;
    flex-wrap: wrap;
    padding: 0 4vw;
}

.portfolio-work-main-container-button button{
    padding: 2vh 2vw;
    font-size: 1rem;
    cursor: pointer;
    border-radius: 25px;
    border: 2px solid #612d91;
    background-color: #fff;
    color: #000;
    font-weight: bold;
}