.portfolio-modal-two-main-container-first{
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0.014) 0%, rgba(0, 0, 0, 0) 100%), radial-gradient(at top center, rgba(255, 255, 255, 0.123) 0%, rgba(0, 0, 0, 0.041) 120%) #989898c4;
  z-index: 2;
}

.portfolio-modal-two-main-container {
  position: fixed;
  top: 57.5%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 60vw;
  height: 70vh;
  z-index: 3;
  display: flex;
  align-items: center;
  justify-content: center;
}

.portfolio-modal-two-main-container img {
  width: 100%;
  max-height: 80vh;
}

@media only screen and (max-width: 767px) {
  .portfolio-modal-two-main-container {
    width: 85vw;
    height: 50vh;
  }
}
