.portfolio-cards-row {
  padding: 0 1vw;
  margin-top: 7.5vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  position: relative;
}

.portfolio-cards-row-card-one {
  width: 30vw;
  margin: 0 1vw;
  position: relative;
  margin-bottom: 2vw;
  overflow: hidden;
}

.portfolio-cards-row-card-one img {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
}

.portfolio-cards-row-card-one-div-two {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  cursor: pointer;
  background-color: #00000075;
  filter: brightness(0%);
  transition: all 0.3s ease-in;
}

.portfolio-cards-row-card-one-div-two:hover {
  opacity: 1;
  filter: brightness(75%);
}

.load-more-button-div {
  display: flex;
  align-items: center;
  justify-content: center;
}

.load-more-button {
  background-color: #612d91;
  color: #fff;
  padding: 2vh 2vw;
  border: none;
  border-radius: 25px;
  font-size: large;
  font-weight: bold;
  align-self: center;
}

.video-category{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin: 7.5vh 0;
  padding: 0 5vw;
  gap: 3vw;
}

@media only screen and (max-width: 600px) {
  .portfolio-cards-row {
    display: flex;
    flex-direction: column;
  }

  .portfolio-cards-row-card-one {
    width: 90vw;
    height: 80vw !important;
  }

  .load-more-button {
    font-size: small;
    padding: 2vh 4vw;
  }
}
